<template>
  <!-- <div> -->
  <div @click="openTelegram" v-click-away="closeTelegram">
    <div class="dropdown">
      <a class="cursor-pointer">
        <img
          :src="image"
          :class="imageStyle ? '' : 'lg:w-25 lg:h-25 px-1 w-9 h-7'"
        />
      </a>
      <ul class="dropdown-menu absolute hidden bg-blue-50 pt-1 rounded-lg shadow-2xl m-1" :id="`telegram-dropdown-${scene}`">
        <li class=""><a class="rounded-lg bg-blue-50 hover:text-blue-600 py-2 px-4 block whitespace-no-wrap" href="https://t.me/BlucaENCommunity" target="_blank">
          <div class="flex">
            <div class="lg:w-25 lg:h-25 px-1 w-9 h-7"><img src="@/assets/icon/global_flag.png"/></div>
            <div class="px-3 py-1 lg:text-sm align-middle"><span>Worldwide</span></div>
          </div>
          </a></li>
        <li class=""><a class="rounded-lg bg-blue-50 hover:text-blue-600 py-2 px-4 block whitespace-no-wrap" href="https://t.me/blucaOffiCommu" target="_blank">
          <div class="flex">
            <div class="lg:w-25 lg:h-25 px-1 w-9 h-7"><img src="@/assets/icon/thailand_flag.png"/></div>
            <div class="px-3 py-1 lg:text-sm align-middle"><span>Thailand</span></div>
          </div>
          </a></li>
      </ul>
    </div>

  </div>
</template>

<script>
export default {
  props: {
    image: {
      type: [String],
      default: "",
    },
    imageStyle: {
      type: [Boolean],
      default: false,
    },
    scene: {
      type: [String],
      default: ""
    }
  },
  data() {
    return {
      isDropdownOpen: false
    }
  },
  methods: {
    openTelegram() {
      this.isDropdownOpen = true
      document.getElementById(`telegram-dropdown-${this.scene}`).classList.remove('hidden')
    },
    closeTelegram() {
      if (this.isDropdownOpen) {
        document.getElementById(`telegram-dropdown-${this.scene}`).classList.add('hidden')
        this.isDropdownOpen = false
      }
      // document.getElementById('telegram-dropdown').classList.add('hidden')
    }
  }
};
</script>

