<script>
import RoundButton from "./RoundButton.vue";
import TelegramBtn from "./TelegramBtn.vue";

export default {
  components: {
    RoundButton,
    TelegramBtn
  },
  methods: {
    onToggle() {
      document.getElementById("mobile-menu").classList.toggle("hidden");
    },
    openSideBar() {
      document.getElementById("mobile-menu").classList.remove("hidden");
    },
    onClickAway(event) {
      document.getElementById("mobile-menu").classList.add("hidden");

      // console.log(event)
    },
    scrollToView(id) {
      if (this.$route.path === "/" || this.$route.path === "/roadmap") {
        setTimeout(() => {
          document.getElementById(id).scrollIntoView({
            behavior: "smooth",
            block: "start",
            inline: "start",
          });
        }, 100);
      } else {
        window.scrollTo(0, 0);
      }
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
  },
};
</script>

