<script>
import TelegramBtn from "./TelegramBtn.vue";

export default {
  components: {
    TelegramBtn
  },
  methods: {
    gotoLink(link) {
      window.open(link);
    },
    scrollToView(id) {
      if (this.$route.path === "/" || this.$route.path === "/roadmap") {
        setTimeout(() => {
          document.getElementById(id).scrollIntoView({
            behavior: "smooth",
            block: "start",
            inline: "start",
          });
        }, 100);
      } else {
        window.scrollTo(0, 0);
      }
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
  },
};
</script>
